import React from 'react'
import {
	ProductContainer,
	ProductWrapper,
	ProductHeading,
	ProductTitle,
	ProductCard,
	ProductImg,
	ProductInfo,
	ProductDesc
} from './ProductsElement';

const Products = ({data}) => {
	return (
		<ProductContainer>
			<ProductHeading>ABOUT THE GAME</ProductHeading>
			<ProductWrapper>
			{data.map((product, index) => {
				return(
					<ProductCard key={index}>
						<ProductImg src={product.img} alt= {product.alt}/>
						<ProductInfo>
							<ProductTitle>{product.name}</ProductTitle>
							<ProductDesc>{product.desc}</ProductDesc>
						</ProductInfo>
					</ProductCard>
				)
			})}
			</ProductWrapper>
		</ProductContainer>
	)
}

export default Products
