import React from 'react';
import styled from 'styled-components';
//import { Button } from './Button.component';

const Section = styled.section`
width:100%;
height:70%;
padding:4rem 0rem;
background:#000d1a;
background: ${({primary}) => (primary ? '#000d1a' :'#D8F3FF')};

`;
const Container = styled.div`


display:grid;
grid-template-columns:1fr 1fr;
grid-template-row:800px;

@media screen and (max-width:768px) {
	grid-template-columns:1fr;
}

`;

const ColumnLeft = styled.div`
display:flex;
flex-direction:column;
justify-content:center;
align-items:flex-start;
line-height:1.4;
padding:1rem 2rem;
order:${(reverse) => reverse ? 2 : 1};
margin-left:4rem;


@media screen and (max-width:768px){
	margin-left:0.5rem;
}

h1{
	margin-bottom: 1rem;
	color:#68EBB8;
	font-size: clamp(2rem,6vw, 3.5rem);
}

p{
	margin-bottom:2rem;
	
}
`;
const ColumnRight = styled.div`
padding:1rem 2rem;
order:${({reverse}) => (reverse ? '1': '2')};
display:flex;
justify-content:center;
align-items:center;

@media screen and (max-width:768px){
order:${(reverse) => (reverse ? 2 :1)};
}

img{
	width:100%;
	height:100%;
	object-fit:cover;

	@media screen and (max-width:768px){
		width:90%;
		height:90%;
	}
}
`;


const InfoSection = ({id,heading,paragraphOne,paragraphTwo,buttonLable,reverse,image,primary}) => {
	return (
		<Section primary={primary}>
			<Container id='about'>
				<ColumnLeft >
				<h1 >{heading}</h1>
				<p>{paragraphOne}</p>
				<p>{paragraphTwo}</p>
				
				</ColumnLeft>
				<ColumnRight reverse={reverse}>
				<img src={image} alt="home"/>
				</ColumnRight>
			</Container>
		</Section>
	)
}

export default InfoSection
