import React,{useState} from 'react';
import Navbar from '../Navbar';
import Sidebar from '../Sidebar';

import {
HeroContainer,
HeroContent,
HeroItems,
HeroH1,
HeroP,
HeroBtn
} from './HeroElement';

const Hero = () => {
	const [isOpen, setIsOpen] = useState(false)

	const toggle = () => {
		setIsOpen(!isOpen)
	}

	

	return (
		<HeroContainer>
		<Navbar toggle={toggle}/>
			<Sidebar isOpen={isOpen} toggle={toggle}/>
			
					<HeroItems>
					<HeroH1>GAME IT TILL YOU LEARN IT</HeroH1>
					<HeroP>Are you ready to have a completing mind blowing way of learning!</HeroP>
					<a href='https://drive.google.com/file/d/14Qq_Ec1cTYGJiAaWD_qkbh-6fgcJ9mnL/view?usp=sharing'>
					<HeroBtn>Download</HeroBtn>
					</a>
				</HeroItems>
								
							
			
	</HeroContainer>
);
};
export default Hero;
