import React from 'react'
import styled, { css } from 'styled-components';
import{Link} from 'react-router-dom';

const FooterContainer = styled.div`
background-color: #000d1a;

`;
const FooterWrap = styled.div`
padding:48px 10px;
display:flex;
flex-direction:column;
justify-content: center;
align-items: center;
max-width:2000px;
margin:0 auto;


`;
const FooterLinksContainer = styled.div`
display:flex;
justify-content:space-between;



@media screen and (max-width:820px){
	padding-top:32px;
}
`;

const FooterLinksWrapper = styled.div`
display:flex;

@media screen and  (max-width:820px){
	flex-direction: column;

}

`;
const FooterLinkItems = styled.div`
display:flex;
flex-direction:column;
align-items:flex-start;
margin:16px;
text-align:center;
width:160px;
box-sizing: border-box;
color:#007D52;
cursor:pointer;


@media screen and (max-width:420px ){

	margin:0;
	padding:10px;
	width:100%;
}

`;

const FooterLinkTitle = styled.div`
font-size:18px;
margin-bottom:16px;
font-weight:bold;

`;

// const FooterLink = styled(Link)`
// color:#000d1a;

// margin-bottom:0.5rem;
// font-size:14px;

// &:hover{
// 	color:#000;
// 	transition:0.3s ease-out;
// 	text-decoration: none;
// }
// `;

const SocialMedia = styled.section`
max-width:1000px;
width:100%;

`;

const SocialMediaWrap = styled.div`
display:flex;
flex-direction:column;
justify-content:space-between;
align-items:center;
max-width:1100px;
margin:40px auto 0 auto;



@media screen and (max-width: 820px){
	flex-direction:column;

}
`;
const SocialLogo = styled.div`
color:#000d1a;
justify-self:start;
cursor:pointer;
text-decoration:none;
font-size:2rem;
display:flex;
align-items:center;
margin-bottom:16px;
font-weight:bold;

`;

const WebsiteRights = styled.small`
color:#000d1a;
margin-bottom:16px;


`;



const Footer = () => {
	return (
		<FooterContainer>
			<FooterWrap>
				<FooterLinksContainer>
					<FooterLinksWrapper>
						<FooterLinkItems>
							<FooterLinkTitle>Home</FooterLinkTitle>
								{/* <FooterLink to= "/signin">Bla</FooterLink>
								<FooterLink to= "/signin">Bla</FooterLink>
								<FooterLink to= "/signin">Bla</FooterLink> */}
						</FooterLinkItems>
						<FooterLinkItems>
							<FooterLinkTitle>About Us</FooterLinkTitle>
								{/* <FooterLink to= "/signin">Bla</FooterLink>
								<FooterLink to= "/signin">Bla</FooterLink>
								<FooterLink to= "/signin">Bla</FooterLink> */}
						</FooterLinkItems>
					</FooterLinksWrapper>
					<FooterLinksWrapper>
						<FooterLinkItems>
							<FooterLinkTitle>Services</FooterLinkTitle>
								{/* <FooterLink to= "/signin">Bla</FooterLink>
								<FooterLink to= "/signin">Bla</FooterLink>
								<FooterLink to= "/signin">Bla</FooterLink> */}
						</FooterLinkItems>
						
					</FooterLinksWrapper>
				</FooterLinksContainer>
				<SocialMedia>
					<SocialMediaWrap>
				<SocialLogo to="/">UPP</SocialLogo>
				<WebsiteRights>UPP ©  2021 {new Date().getFullYear()} 
				All rights reserved</WebsiteRights>
				</SocialMediaWrap>
				</SocialMedia>
			</FooterWrap>
		</FooterContainer>
	)
}

export default Footer;

